.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 20vh;
  width: auto;
  background-color: white;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation: slide-down 300ms ease-out forwards;
  max-height: 550px;
  max-width: 780px;
  border-radius: 10px;
  width: 40rem;
  left: calc(50% - 20rem);
}

@media (max-width: 650px) {
  .modal {
    width: 30rem;
    left: calc(50% - 15rem);
  }
}

@media (max-width: 490px) {
  .modal {
    width: 25rem;
    left: calc(50% - 12.5rem);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
